import React, { Component } from 'react'
import { connect } from 'react-redux'
import { Redirect } from 'react-router-dom'

import toast from 'Components/toast/action';

class Logout extends React.Component {
    constructor(props) {
        super(props)
    }
    async componentWillMount() {
        let { user } = this.props
        if (user && user.size > 0) {
            toast('退出登录中...')
            let res = await apicloud('user/logout', {
                method: 'post'
            })
            if (res.success) {
                localStorage.removeItem('authorization')
                this.props.dispatch('user/init', {})
                toast('退出登录成功')
            }
        }
    }
    render() {
        let { location, user } = this.props
        let from = location.from || { pathname: '/' }
        return (
            user && user.size > 0 ?
                null :
                <Redirect to={from} />
        )
    }
}

export default connect((state) => ({ user: state.get('user') }))(Logout)


